@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;300;400;500;600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  font-family: 'Poppins', sans-serif;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 100vh;
  box-sizing: content-box;
}

*{
  box-sizing: content-box;
}

html{
  min-height: 100vh;
}

body {
  background-color: #F3F2F0;
  height: 100%; 
}



.homepage-header {
    color: #2977C9;
    font-size: 32px;
    font-weight: 400;
    line-height: 60px;
    font-family: 'Poppins', sans-serif;
  }
  
  .homepage-about-text {
    font-size: 14px; 
    color: #2977C9;
  }

.website-section {
  margin-left: 30px; 
  margin-right: 30px;
}
.under-picture-section {
    display: flex; 
}

.action-buttons-section {
    background-color:#2977C9; 
    border: none; 
    border-radius: 6px; 
    height: 140px;
    width: 200px; 
    padding-top: 5px; 
    padding-left: 25px;
    padding-right: 25px; 
    margin-left: 10px;
    display: grid; 
    margin-top: 10px; 
}

.action-button-style {
    margin-top: 10px; 
    font-family: 'Poppins', sans-serif;
    display: block;
    background-color: white; 
    width: 190px;
    border-radius: 8px; 
    border: none; 
    cursor: pointer;
    height: 25px; 
}

.favorite-button > .heart-icon {
    color: white; 
}

.favorite-button {
    font-family: 'Poppins', sans-serif;
    display: block;
    background-color: #2977C9; 
    border: 2px solid white !important; 
    color: white; 
    width: 190px;
    border-radius: 8px; 
    border: none; 
    cursor: pointer;
    height: 25px; 
}

.action-buttons {
    margin-top: 10px; 
}

.edit-pet {
    background-color: #2977C9;
    color: white; 
    margin-left: 35px;

}

.pet-image-heading {
    margin-top: 5px; 
    width: 50%; 
    height: 50%;
    border-radius: 4px; 
}
.about-pet-section {
    border: none; 
    background-color: white; 
    border-radius: 6px; 
    font-family: 'Poppins', sans-serif;
    width: 300px; 
    color: rgb(65, 63, 63); 
    padding-top: 5px; 
    padding-left: 25px;
    padding-right: 25px; 
}

.pet-card-dot {
    margin-left: 2.5px; 
    margin-right: 2.5px; 
    margin-bottom: .5px;
}

.pet-name-heading {
    font-weight: bolder; 
}

.pet-subsection-details {
    font-size: 10px; 
    margin-top: -8px;
    padding-bottom: 8px; 
    border-bottom: 0.5px solid black; 
}


.about-me-section {
    border-bottom: 0.5px solid black; 
}
.about-me-section, .meet-me-section {
    font-weight: bolder; 
    font-size: 14px;
}
.about-me-section > h2, .meet-me-section > h2 {
    margin-bottom: 2px; 
    margin-top: 3px;
}

.about-subheadings {
    font-weight: bold; 
    font-size: 12px;
    margin: 0; 
    margin-bottom: 2px; 
}

.about-subheadings-answers, .meet-me-subheadings-answers {
    font-size: 10px;
    font-weight: normal;
}

.about-subheadings-answers {
    display: inline; 
}

.color, .diet-rest{
    display: inline; 
}
.find-a-pet-label {
    display: block;
    background-color:#2977C9; 
    border-radius: 8px; 
    border: none; 
    color: white; 
    cursor: pointer;
    width: 100px; 
    text-decoration: none; 
    display: inline-block; 
}

.unselected-buttons {
    margin-top: 10px; 
    padding: 5px; 
    background-color: rgb(168, 166, 168); 
    border-radius: 2px; 
    color: white; 
    border: none; 
    cursor: pointer;
    text-decoration: none; 
}

.selected-btn {
    margin-top: 10px; 
    padding: 5px; 
    background-color: #2977C9; 
    border-radius: 2px; 
    border: none; 
    color: white; 
    cursor: pointer;
    text-decoration: none; 
}
.pet-card {
    width: 12rem; 
    background-color: white; 
    border-radius: 4px; 
    margin-top: 10px; 
    box-shadow: 0px -1px 1px rgba(0,0,0,0.08), 1px 0px 1px rgba(0,0,0,0.08), -1px 0px 1px rgba(0,0,0,0.08), 0px 1px 1px rgba(0,0,0,0.08);
    margin-right: 5px; 
    margin-left: 5px; 
}

.pet-card-dot {
    margin-top: -2px;
    font-size: 5px;
}

.pet-image {
    width: 100px; 
    margin-left: 5px; 
}

.pet-name {
    color: #2977C9;
    font-weight: bold; 
}

.pet-details {
    font-size: 12px; 
}

.pet-card-button {
    margin-bottom: 5px; 
    display: block;
    background-color:#2977C9; 
    border-radius: 4px; 
    border: none; 
    color: white; 
    cursor: pointer;
    width: 170px; 
    font-size: 12px; 
    text-decoration: none; 
    display: inline-block; 
}

.favorite-button {
    border-radius: 50%; 
    width: 30px; 
    height: 30px;
    background-color: #5fa2e9;
    float: right; 
}

.heart-icon {
    color:  #2977C9; 
    font-size: 20px;
    padding-top: 2px; 
}

.saved-heart-icon {
    color: red; 
}

.unsaved-heart-icon {
    color:  gray; 
}

.hover-button:hover {
    cursor: pointer;
}

.general-icon {
    font-size: 20px;
    padding-top: 2px; 
}
.left-header-section {
    display: inline-flex;
}
.left-header-labels {
    margin-top: 8px; 
}
.navbar-header-content {
    background-color:#2977C9;
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 65px;
    width: -webkit-fill-available;
}

.dropdown-labels  {
    color: white; 
    cursor: pointer; 
    font-family: 'Poppins', sans-serif;
    background-color: #1b508a;
    font-size: 10px; 
    border: none; 
    text-decoration: none; 
}


.dropdown-labels:hover  {
    background-color: #2466ad;
}
.nav-bar-labels {
    color: white; 
    font-size: 10px; 
    padding-left: 40px; 
    text-decoration: none; 
    cursor: pointer; 
}

.nav-bar-labels:hover, .username-labels:hover {
    color: black; 
}

.logo-image {
    width: 100px; 
    margin-left: 5px; 
}

.navbar-icons {
    padding-right: 2px; 
}

.username-label {
    margin-top: 6px; 
    text-decoration: none; 
    margin-right: 20px;
    border: none; 
    color: white; 
    font-size: 10px; 
    text-decoration: none; 
    background-color:#2977C9;
    font-family: 'Poppins', sans-serif;
    padding-right: 45px;
    cursor: pointer;  
}

.dropdown-section {
    display: grid; 
    margin-right: 30px;
    border-radius: 4px;
}

.right-side-navbar {
    display: flex; 
}

.heart-navbar-icon {
    font-size: 20px; 
    color: white; 
    margin-right: 15px; 
    margin-top: 5px;
    cursor: pointer; 
}

.admin-labels {
    display: inline-grid; 
}
.form-section {
    margin-top: 10px; 
}

.update-profile-button {
    background-color:#2977C9;; 
    color: white; 
    padding: 5px; 
    margin-bottom: 12px; 
    border-radius: 5px;
    border: 1px; 
    box-shadow: 0px -1px 1px rgba(0,0,0,0.08), 1px 0px 1px rgba(0,0,0,0.08), -1px 0px 1px rgba(0,0,0,0.08), 0px 1px 1px rgba(0,0,0,0.08);
    width: 140.5px;
    cursor: pointer;
}
.search-overall-section {
    padding: 10px 20px 25px;
    margin-top: 12px; 
    background-color: white; 
    border-radius: 10px;
    box-shadow: 0px -1px 1px rgba(0,0,0,0.08), 1px 0px 1px rgba(0,0,0,0.08), -1px 0px 1px rgba(0,0,0,0.08), 0px 1px 1px rgba(0,0,0,0.08)
}

.search-pets-input {
    width: 500px;
    height: 30px;
    border-radius: 10px; 
    color: black; 
    border: 0.5px solid gray; 
}

.search-pets-input::-webkit-input-placeholder {
    font-size: 12px; 
}

.search-pets-input:-ms-input-placeholder {
    font-size: 12px; 
}

.search-pets-input::placeholder {
    font-size: 12px; 
}

.search-pets-input:hover {
    border: 1px solid #2977C9; 
}

.search-page-checkboxes {
    display: inline-flex; 
    font-size: 10px; 
}

.pet-cards-results-section {
    display: inline-block; 
}
.basic-search-section {
    margin-bottom: 12px; 
    background-color: #2977C9;
    height: 100px; 
    width: 450px;
    border-radius: 10px; 
    color: white; 
    padding-left: 5px; 
    padding-right: 0px; 
}


.find-by-animal-button {
    background-color: #2977C9;
    color: white; 
    font-size: 12px; 
    border: none; 
    width: 90px; 
    height: 70px; 
    font-family: 'Poppins', sans-serif;
    padding-right: 30px;
    border-right: 1px solid #1a4b80;
}
.other-pets-button {
    background-color: #2977C9;
    color: white; 
    font-size: 12px; 
    border: none; 
    width: 90px; 
    height: 70px; 
    font-family: 'Poppins', sans-serif;
    padding-right: 30px;
}

.find-by-animal-button:hover, .other-pets-button:hover {
    background-color: #2466ad;
}

.selected-button {
    background-color: white;
    color:#2977C9; 
    font-size: 12px; 
    border: none; 
    width: 90px; 
    height: 70px; 
    font-family: 'Poppins', sans-serif;
    padding-right: 30px;
}

.animal-icon {
    padding-right: 3px;
    font-size: 20px; 
}
.basic-search-title {
    margin-right: 10px; 
    margin-bottom: 0px; 
}

.basic-search-button {
    display: block;
    background-color:#2977C9; 
    width: 450px;
    border-radius: 8px; 
    border: none; 
    color: white; 
    cursor: pointer;
}


.advanced-search-section {
    margin-bottom: 8px; 
    background-color: #2977C9;
    height: 170px; 
    width: 450px;
    border-radius: 10px; 
    color: white; 
    padding-left: 5px; 
    padding-right: 0px; 
    display: flex; 
}

.advanced-search-titles {
    font-weight: bold; 
    font-size: 14px;
}
.advanced-search-options {
    font-size: 12px;
    display: flex; 
}

.individual-adv-search-sections {
    padding: 10px; 
}

.search-button {
    margin-left: 60px; 
    margin-top: 70px; 
    display: block;
    background-color:white; 
    border-radius: 4px; 
    border: none; 
    color:black; 
    cursor: pointer;
    font-size: 12px;
    padding: 4px;
    width: 45px; 
    font-family: 'Poppins', sans-serif;
}


.adv-search-name-input {
    width: 100px;
}

.modal {
    border-radius: 10px;
    box-shadow: 0 2px 4px rgba(0, 0, 0, .1), 0 8px 16px rgba(0, 0, 0, .1);
    text-align: center;
    flex-direction: column;
    width: 200px !important;
    background-color: aquamarine;
    font-family: 'Poppins', sans-serif;
}

.entry-button {
    background-color:#2977C9;; 
    color: white; 
    padding: 5px; 
    margin-bottom: 12px; 
    border-radius: 5px;
    border: 1px; 
    box-shadow: 0px -1px 1px rgba(0,0,0,0.08), 1px 0px 1px rgba(0,0,0,0.08), -1px 0px 1px rgba(0,0,0,0.08), 0px 1px 1px rgba(0,0,0,0.08);
    width: 140.5px;
    cursor: pointer;
}

.entry-info-section {
    height: 22px; 
    width: 300px;
    margin-top: 5px;
    margin-bottom: 9px; 
    border-radius: 3px; 
    border: 0.5px solid gray;
}

.entry-info-section::-webkit-input-placeholder {
    color: gray;
    font-size: 10px;
}

.entry-info-section:-ms-input-placeholder {
    color: gray;
    font-size: 10px;
}

.entry-info-section::placeholder {
    color: gray;
    font-size: 10px;
}

.form-section {
    background-color: lightgray;
    border-radius: 3px;
}


.homepage-buttons-section {
    display: inline-grid;
    margin: 16px;  
}

.homepage-buttons-section > button { 
    padding: 12px; 
    margin-bottom: 12px; 
    border-radius: 5px;
    border: 1px; 
    box-shadow: 0px -1px 1px rgba(0,0,0,0.08), 1px 0px 1px rgba(0,0,0,0.08), -1px 0px 1px rgba(0,0,0,0.08), 0px 1px 1px rgba(0,0,0,0.08)

}

.homepage-buttons-section > button:hover {
  box-shadow: 0px 4px 6px rgba(0,0,0,0.15), 0px 0px 2px rgba(0,0,0,0.15)
}

.add-pet-button {
    background-color:#2977C9;; 
    color: white; 
    padding: 5px; 
    border-radius: 5px;
    border: 1px; 
    box-shadow: 0px -1px 1px rgba(0,0,0,0.08), 1px 0px 1px rgba(0,0,0,0.08), -1px 0px 1px rgba(0,0,0,0.08), 0px 1px 1px rgba(0,0,0,0.08);
    width: 140.5px;
    margin-top: 5px; 
    margin-bottom: 5px; 
    cursor: pointer;
}
.sub-heading {
    font-size: 12px; 
}

.checkboxes {
