    display: inline; 

}

.sub-heading {
    font-size: 12px; 
}

.checkboxes {
    font-size: 10px; 
    display: inline; 

}

.user-card {
    background-color: white; 
    border-radius: 4px; 
    margin-top: 10px; 
    box-shadow: 0px -1px 1px rgba(0,0,0,0.08), 1px 0px 1px rgba(0,0,0,0.08), -1px 0px 1px rgba(0,0,0,0.08), 0px 1px 1px rgba(0,0,0,0.08);
